<template>
    <div id="contain">
        <div class="header">
            <div class="Input">
                <el-input class="title" placeholder="题目" v-model="title" maxlength="15"></el-input>
                <el-input class="publisher" placeholder="发布者" v-model="publisher" maxlength="10"></el-input>
            </div>
            <div class="publish">
                <input accept="*.pdf,*.txt,*.doc" type="file" name="image" @change="getFile($event)" />
                <el-button type="success" round @click="publish()">确认修改</el-button>
            </div>
        </div>
        <div class="write">
          <quill-editor
            class="mr-8"
            max-height="800"
            v-model="content"
            ref="myQuillEditor"
            style="height: 750px; width:1300px; margin:20px auto;"
            @change="onEditorChange($event)"
          >
          </quill-editor> 
         
        </div>
        
    </div>
</template>

<script>
import { quillEditor } from 'vue-quill-editor'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
export default {
    data(){
        return{
            content:null,
            title:'',
            publisher:'',
            url:''
        }
    },
    methods: {
        onEditorChange({ html }) {
            this.content = html
        },
        getFile: function (event) {
            this.file = event.target.files[0];
        },
        publish(){
            this.$confirm('是否确认发表?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'info'
                }).then(() => {
                    var url;
                    let formdata = new FormData();
                    formdata.append("File",this.file);
                    this.$http.post('boss/upload',formdata).then((res)=>{
                        url=res.data.data
                        let announce={
                        id:this.$route.params.newsId,
                        title:this.title,
                        publisher:this.publisher,
                        context:this.content,
                        fileURL:url
                    };
                    this.$http.post('/boss/updatenews',{
                        news:announce
                    }).then(()=>{
                        this.$message({
                            type: 'success',
                            message: '提交成功!'
                            });
                        this.content=null;
                        this.$router.go(-1)
                    })
                    })
                }).catch(() => {
                this.$message({
                    type: 'warning',
                    message: '发布失败'
                });          
                });
        },
        getData(){
            this.$http.get('boss/readNews',{
                params:{
                    id:this.$route.params.newsId
                }
            })
            .then((res)=>{
                let data=res.data.data;
                this.title=data.title;
                this.publisher=data.publisher;
                this.content=data.context;
            })
        }
    },
    components: {
        quillEditor
    },
    mounted(){
        this.getData();
    }
}
</script>

<style scoped>
#contain .write .quill-editor{
    background: white;
}
.header{
    width: 50%;
    height: 120px;
    margin: 10px auto; 
}
.header .el-input{
    margin-top:5px ;
}
#contain{
    width: 100%;
    min-height: 100%;
    background: rgb(234,237,241);
}
#contain  .el-button.is-round{
    margin: 0px 10px;
}
.publish{
    width: 50%;
    position: relative;

    height: 100px;
    left: 80%;
}
Input{
    width: 50%;
}
</style>